<template>
    <div class="layer">
        <div class="layer-shift">
            <template v-for="(item, index) in shiftButton">
                <el-button type="text" :key="index" @click="moveCard($event, index)">
                    <i :class="'icon-' + shiftButton[index]" class="icon iconfont"></i>
                </el-button>
            </template>
        </div>
        <div class="layer-list" v-if="config.sequence && config.sequence.length">
            <template v-for="(id, index) in config.sequence">
                <div v-if="config.components[id]"
                     :class="calcActiveStyle(id)"
                     :key="index"
                     class="item"
                     @click.exact="activeSingleBox($event, id)"
                     @click.ctrl.left="activeMultiBox($event, id)"
                     @click.meta.left="activeMultiBox($event, id)">
                    <img :src="getComponentImage(id)">
                    <span>{{config.components[id].text}}</span>
                </div>
            </template>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Layer",
        props: ["config", "state"],
        data() {
            return {
                shiftButton: ["top", "up", "down", "bottom"]
            }
        },
        methods: {
            /**
             * 获取组件缩略图
             */
            getComponentImage(id) {
                let item = this.config.components[id]
                if (item.image) {
                    return item.image
                }
                return './img/components/' + item.category + '/' + item.name + '.png'
            },
            /**
             * 判断当前组件是否选中
             */
            calcActiveStyle(id) {
                return this.state.select[id] ? 'active' : '';
            },
            /**
             * 点击选中当前组件
             */
            activeSingleBox(e, id) {
                e.stopPropagation()
                let comps = {}
                comps[id] = this.config.components[id]
                this.$set(this.state, "select", comps)
            },
            /**
             * 点击选中当前组件
             */
            activeMultiBox(e, id) {
                e.stopPropagation()
                let item = this.config.components[id]
                if (this.state.select[id]) {
                    this.$delete(this.state.select, id)
                } else {
                    this.$set(this.state.select, id, item)
                }
            },
            /**
             * 移动选中的组件卡片
             * @param index
             */
            moveCard(e, btnIndex) {
                e.stopPropagation()
                let idArr = Object.keys(this.state.select)
                if (idArr.length > 1) {
                    return
                }
                let id = idArr[0]
                let sequence = this.config.sequence
                let seqIndex
                let arr
                for (let i = 0; i < sequence.length; i++) {
                    if (sequence[i] === id) {
                        seqIndex = i
                        break
                    }
                }
                switch (btnIndex) {
                    case 0:
                        if (seqIndex === 0) {
                            return
                        }
                        this.config.sequence.splice(seqIndex, 1)
                        this.config.sequence.unshift(id)
                        break
                    case 1:
                        if (seqIndex === 0) {
                            return
                        }
                        arr = this.config.sequence.slice(seqIndex - 1, seqIndex + 1).reverse()
                        this.config.sequence.splice(seqIndex - 1, 2, arr[0], arr[1])
                        break
                    case 2:
                        if (seqIndex === sequence.length - 1) {
                            return
                        }
                        arr = this.config.sequence.slice(seqIndex, seqIndex + 2).reverse()
                        this.config.sequence.splice(seqIndex, 2, arr[0], arr[1])
                        break
                    case 3:
                        if (seqIndex === sequence.length - 1) {
                            return
                        }
                        this.config.sequence.splice(seqIndex, 1)
                        this.config.sequence.push(id)
                        break
                    default:
                        break
                }
            }
        }
    }
</script>

